.main {
  width: 100%;
  min-height: 100%;
  background-image: url(/static/media/bg.a986d735.jpg);
  background-attachment: fixed;
  background-position: center;
}
.main .header {
  width: 100%;
  height: 60px;
  text-align: center;
  line-height: 60px;
  background: rgba(0, 0, 0, 0.21);
}
.main .header h1 {
  color: #fff;
}
.main .container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 150px;
}
.main .container .login-container {
  width: 500px;
  height: 300px;
  border: 1px solid rgba(0, 0, 0, 0.21);
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.21);
  padding: 20px 40px;
}
.main .container .login-container h2 {
  color: #fff;
  text-align: center;
  font-weight: bolder;
}
.main .container .login-container .btn {
  width: 100%;
}
